@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/size";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";

$name: Card;

@mixin textBody {
    @include Font.set($size: delta, $weight: bold, $family: "heading");
}

@mixin cardMobile {
    @include Padding.set($preset: beta);
    @include Margin.set($top: auto, $right: auto, $bottom: none, $left: auto);
    max-width: var(--card-mobile-max-width);
    min-height: var(--card-mobile-min-height);
}

@mixin cardDesktop {
    @include screen.lg {
        @include Margin.set($top: gamma);
        max-width: var(--card-desktop-max-width);
        min-height: var(--card-desktop-min-height);
    }
}

@mixin footer {
    & > footer {
        .secondaryTextLink {
            @include Color.set($fg: "primary");
            @include Font.set($size: gamma, $weight: medium, $family: "default");
        }

        #{Button.$selector} {
            @include Flex.set($align: center);
            @include Color.set($bg: "transparent");
            @include Border.set($style: default);
            cursor: pointer;
        }
    }
}

:export {
    NAME_CARD: $name;
}
