@use "." as this;
@use ".." as super;

@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Layout";

#{this.$selector} {
    @include Flex.set($dir: column, $align: center);
    @include screen.lg {
        @include Flex.set($dir: row, $align: center);
    }
    .cardTenure {
        @include Flex.set($align: "center", $justify: "center", $dir: column);
        @include Padding.set($preset: beta);
        gap: Layout.get-space(alpha);
        min-height: 140px;
        max-width: 328px;

        #{Text.$selector}.title {
            @include Font.set($align: center, $weight: bold);
            @include Color.set($fg: "black");
        }

        svg path {
            @include Color.set($svg-fg: "gray-600");
        }
        div {
            a {
                @include Display.set($display: block);
            }
            #{Text.$selector} {
                @include Font.set($align: center);
            }
            #{Text.$selector}.url {
                @include Margin.set($preset: auto);
                @include Font.set($deco: underline);
                max-width: 240px;
                overflow-wrap: break-word;
            }
        }
    }
}
